
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import toolkit from '../toolkit'
export default {
components: {
Group,
Cell,
CellBox
},
data() {
return {
showContent1: false,
showContent2: false,
tel:'400-888-9730'
}
},
created() {
if(toolkit.GetQueryString("showContent"))
{
var showContent=toolkit.GetQueryString("showContent");
if(showContent=="1"){
this.showContent1 = true;
}
else if(showContent=="2"){
this.showContent2 = true;
}
}
if(toolkit.GetQueryString("tel")){
this.tel=toolkit.GetQueryString("tel");
}
}
}



