
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import moment from 'moment'
import toolkit from '../toolkit'
import config from '../config'
import '@/assets/css/app.css'

export default {
components: {
Group,
Cell,
CellFormPreview,
LoadMore,
Loading,
Box,
XButton,
Flexbox,
FlexboxItem
},
data() {
return {
channel: "",
openId: "",
phone: "",
code: "",
bindRes: 0,//0 1 2
needPwdCount: 0,//
leaseList: [],
leaseDetailPage: false,
leaseDetail: {},
loadingShow: false,
leaseClose: false,
settlementUnit: "",
rentCycle: ""
}
},
created() {
this.openId = toolkit.GetQueryString("openId");
this.channel = toolkit.GetQueryString("channel");
this.bindRes = toolkit.GetQueryString("bindRes");
this.phone = toolkit.GetQueryString("phone");
this.code = toolkit.GetQueryString("code");
this.needPwdCount = toolkit.GetQueryString("needPwdCount");

if (toolkit.GetQueryString("leaseId")) {
var leaseId = parseInt(toolkit.GetQueryString("leaseId"));
this.leaseClose = true;
this.getLeaseDetail(leaseId);
} else {
if (this.bindRes == 0) {
this.$vux.toast.text(
""
);
} else
if (this.bindRes == 1) {
this.$vux.toast.text(""
);
} else if (this.bindRes == 2) {
this.$vux.toast.text(""
);
}
this.getLeaseList();

}

},
methods: {
getLeaseList() {
var info = {
channel: this.channel,
openId: this.openId
}
toolkit.showLoading(this.$vux);

toolkit.sendPost(config.serverBaseUrl + "/leaselist", info, (err, result) => {
if (err) {
this.$vux.alert.show({
title: '',
content: err
})
} else {
var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
} else {
this.setLeaseList(obj);
}
}
});
},
setLeaseList(obj) {

if (obj.length > 0) {
for (var i = 0; i < obj.length; i++) {
var lease = obj[i];
var leaseJson = {
roomName: lease.houseName + "-" + lease.roomName,
leaseId: lease.leaseId,
list: [],
house: [],
};


leaseJson.list.push({
label: "",
value: moment(lease.leaseBeginDate).format('YYYY-MM-DD') + "" + moment(lease.leaseEndDate).format('YYYY-MM-DD')
});

leaseJson.list.push({
label: "",
value: lease.rent.toFixed(2)
});
leaseJson.list.push({
label: "",
value: lease.deposit.toFixed(2)
});
this.leaseList.push(leaseJson);
}
}
toolkit.hideLoading(this.$vux);
},
getLeaseDetail(leaseId) {
var info = {
channel: this.channel,
openId: this.openId,
leaseId: leaseId
}
toolkit.showLoading(this.$vux);
toolkit.sendPost(config.serverBaseUrl + "/leaseinfo", info, (err, result) => {
if (err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {

var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {
this.leaseDetail = obj;
this.leaseDetail.leaseBeginDate = moment(this.leaseDetail.leaseBeginDate).format('YYYY-MM-DD');
this.leaseDetail.leaseEndDate = moment(this.leaseDetail.leaseEndDate).format('YYYY-MM-DD');


if (this.leaseDetail.settlementUnit != 4) {
if (this.leaseDetail.settlementUnit == 1) {
this.settlementUnit = "";
} else if (this.leaseDetail.settlementUnit == 2) {
this.settlementUnit = "";
} else if (this.leaseDetail.settlementUnit == 3) {
this.settlementUnit = "";
}
if (this.leaseDetail.depositNum == 0) {
this.leaseDetail.depositNum = "";
} else {
this.leaseDetail.depositNum = " " + this.leaseDetail.depositNum + this.settlementUnit;
}
}

this.leaseDetailPage = true;
toolkit.hideLoading(this.$vux);
}
}
});
},
backLeaseList() {
this.leaseDetailPage = false;
},
closeLeaseDetail() {
wx.closeWindow();
},
actionBind() {
let url = window.location.origin + window.location.pathname + "?channel=" + this.channel + "&openId=" + this.openId;
if (this.needPwdCount > 0) {
window.location.replace(url + "&pageCategory=7&phone=" + this.phone + "&code=" + this.code)
} else {
window.location.replace(url + "&pageCategory=6");
}

}

}
}



