const Utils = {
	/**
	 * []
	 * @param  {[type]} time [description]
	 */
	formateTime(time, format) {
		var timer = new Date(time)
		// var format = 'yyyy-MM-dd hh:mm:ss'
		var date = {
			'M+': timer.getMonth() + 1,
			'd+': timer.getDate(),
			'h+': timer.getHours(),
			'm+': timer.getMinutes(),
			's+': timer.getSeconds(),
			'q+': Math.floor((timer.getMonth() + 3) / 3),
			'S+': timer.getMilliseconds()
		}
		if(/(y+)/i.test(format)) {
			format = format.replace(RegExp.$1, (timer.getFullYear() + '').substr(4 - RegExp.$1.length))
		}
		for(var k in date) {
			if(new RegExp('(' + k + ')').test(format)) {
				format = format.replace(RegExp.$1, RegExp.$1.length === 1 ?
					date[k] : ('00' + date[k]).substr(('' + date[k]).length))
			}
		}
		return format
	},
	/**
	 * @param {string} url, url
	 * @param {string} name, 
	 * @returns
	 */
	getUrlParam(url, name) {
		var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
		var r = url.split('?')[1].substr(0).match(reg); //
		if(r != null) return unescape(r[2]);
		return null; //
	},
	// ID
	getAvatarUrl: function(kkId) {
		if(kkId) {
			return utils.avatarUrl + '?userId=' + encodeURIComponent(kkId);
		} else {
			return 'assets/css/img/default-user.png';
		}
	},

	createXMLHttpRequest() {
		//XMLHttpRequest
		var xmlrequest;
		if(window.XMLHttpRequest) {
			//DOM 2
			xmlrequest = new XMLHttpRequest();
		} else if(window.ActiveXObject) {
			// IE
			try {
				xmlrequest = new ActiveXObject("Msxml2.XMLHTTP");
			} catch(e) {
				try {
					xmlrequest = new ActiveXObject("Microsoft.XMLHTTP");
				} catch(e) {}
			}
		}
		return xmlrequest;
	},

	sendPost(urlStr, data, cb) {
		var xmlrequest = this.createXMLHttpRequest();
		if(!xmlrequest) {
			cb("Cann't create request!");
		} else {
			var dataStr = "";
			var bFirst = true;
			for(var key in data) {
				if(bFirst) {
					bFirst = false;
					dataStr = key + "=" + data[key];
				} else {
					dataStr = dataStr + "&" + key + "=" + data[key];
				}
			}

			//
			xmlrequest.open("POST", urlStr, true);
			xmlrequest.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
			xmlrequest.withCredentials = true;

			//
			xmlrequest.onreadystatechange = function processResponse() {
				//
				if(xmlrequest.readyState == 4) {
					if(xmlrequest.status == 200) {
						var response = xmlrequest.responseText;
						cb(null, response);
					} else {
						//
						cb("" + xmlrequest.responseText);
					}
				}
			};
			//
			xmlrequest.send(dataStr);
		}
	},

	GetQueryString(name) {
		var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
		var r = window.location.search.substr(1).match(reg);
		if(r != null) return unescape(r[2]);
		return null;
	},
	GetSettlementUnit(settlementUnit) {
		switch(settlementUnit) {
			case 1:
				return "";
				break;
			case 2:
				return "";
				break
			case 3:
				return "";
				break
		}
	},

	GetFeeUnitText(feeType, feeUnit) { //FeeUnit
		var feeUnitText = "";
		if(feeType == "1") {
			switch(feeUnit) {
				case "0":
				case 0:
					feeUnitText = "";
					break;
				case "1":
				case 1:
					feeUnitText = "";
					break;
				case "2":
				case 2:
					feeUnitText = "";
					break;
			}
		} else if(feeType == "0" || feeType == "3" || feeType == 1 || feeType == 3) {
			feeUnitText = "";
		}

		return feeUnitText;
	},
	showLoading(vux) {
		vux.loading.show({
			text: '',
		});
	},
	hideLoading(vux) {
		vux.loading.hide(2000);
	},
	trim(str) {
		return str.replace(/^(\s|\xA0)+|(\s|\xA0)+$/g, '');
	},
	DateDiff(sDate1, sDate2) { //sDate1sDate22006-12-18  
		var aDate, oDate1, oDate2, iDays
		aDate = sDate1.split("-")
		oDate1 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0]) //12-18-2006  
		aDate = sDate2.split("-")
		oDate2 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0])
		iDays = parseInt(Math.abs(oDate1 - oDate2) / 1000 / 60 / 60 / 24) //  
		return iDays
	}

}
export default Utils


