
import XButton from 'vux/src/components/x-button/index.vue'

import '@/assets/css/app.css'
import toolkit from "../toolkit";
import config from "../config";
export default {
components: {
XButton
},
data() {
return {
channel: "",
openId: "",

phoneNum: "17724747604",
veriftyCode: "869446",

txtCodeLabel: "",
countdDown: 0
};
},
created() {
this.openId = toolkit.GetQueryString("openId");
this.channel = toolkit.GetQueryString("channel");
},
methods: {
getVeriftyCode() {
if (!this.checkPhone()) return;

let info = {
channel: this.channel,
openId: this.openId,
phone: this.phoneNum,
path: 1
};
toolkit.showLoading(this.$vux);
toolkit.sendPost(
config.serverBaseUrl + "/getSMSCode",
info,
(err, result) => {
if (err) {
this.$vux.alert.show({
title: "",
content: err
});
} else {
var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: "",
content: obj.errMsg
});
} else {
this.setCountDown();
}
}
toolkit.hideLoading(this.$vux);
}
);
},

setCountDown() {
this.countdDown = 60;
setInterval(() => {
this.countdDown--;
if (this.countdDown > 0) {
this.txtCodeLabel = "(" + this.countdDown + "s)";
} else {
this.txtCodeLabel = "";
}
}, 1000);
},
bindLease() {
if (!this.checkPhone()) return;
if (!this.checkCode()) return;
this.actionBind();
},
actionBind() {
let info = {
channel: this.channel,
openId: this.openId,
phone: this.phoneNum,
path: 1,
code: this.veriftyCode
};
toolkit.showLoading(this.$vux);
toolkit.sendPost(
config.serverBaseUrl + "/verifyCode",
info,
(err, result) => {
if (err) {
this.$vux.alert.show({
title: "",
content: err
});
} else {
var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: "",
content: obj.errMsg
});
} else {
let bindObj = obj;
console.log(obj);
debugger
let url = window.location.origin + window.location.pathname + "?channel=" + this.channel + "&openId=" + this.openId
+ "&phone=" + this.phoneNum + "&code=" + (bindObj.code ? bindObj.code : this.veriftyCode)
if (bindObj.needPwdCount > 0 && bindObj.newBindCount == 0) {
window.location.href = url + "&pageCategory=7";
} else {
let bindRes;
if (bindObj.newBindCount > 0) {
bindRes = 0;
} else if (bindObj.matchCount === 0) {
bindRes = 1;
}
else if (bindObj.matchCount === bindObj.bindCount) {
bindRes = 2;
}
window.location.replace(url + "&pageCategory=0&bindRes=" + bindRes + "&needPwdCount=" + bindObj.needPwdCount)
}

}
}
toolkit.hideLoading(this.$vux);
}
);


},
checkPhone() {
if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNum)) {
this.$vux.toast.show({
text: "",
type: "warn",
width: "20em"
});
return false;
}
return true;
},
checkCode() {
if (this.veriftyCode === "") {
this.$vux.toast.show({
text: "",
type: "warn",
width: "20em"
});
return false;
}
return true;
}
}
};



