import leaseList from './components/leaseList'	
import snapList from './components/snapList'
import repairList from './components/repairList'
import noticeList from './components/noticeList'
import help from './components/help'
import noticeDetail from './components/noticeDetail'
import bindLease from './components/bindLease'
import bindCode from './components/bindCode'
import config from './config'
import toolkit from './toolkit'

export default {
	name: 'app',
	components: {
		leaseList,			
		snapList,
		repairList,
		noticeList,
		help,
		noticeDetail,
		bindLease,
		bindCode
	},
	data() {
		return {
			channel: "",
			pageCategory: 0  //012...
		}
	},
	created() {
		this.pageCategory = parseInt(toolkit.GetQueryString("pageCategory"));
		this.channel = toolkit.GetQueryString("channel");
		toolkit.sendPost(config.serverBaseUrl + "/getAppName", { channel: this.channel }, (err, result) => {
			if (err) {
				this.$vux.alert.show({
					title: '',
					content: err
				})
			} else {
				var obj = JSON.parse(result);
				if (obj.errCode) {
					this.$vux.alert.show({
						title: '',
						content: obj.errMsg
					})
				} else {
					document.title = obj.appName;
				}
			}
		});
	}
}




