
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

import moment from 'moment'
import toolkit from '../toolkit'
import config from '../config'

export default {
components: {
Group,
Cell,
LoadMore,
Divider,
XButton,
Box
},
data() {
return {
channel: "",
openId: "",
lists: [],
detialView: false,
scrollDisable: false,
noMoreData: false,
pageIndex: 1,
pageCount: 20,
noticeDetail: {},
noticeClose: false
}
},
created() {
this.channel = toolkit.GetQueryString("channel");
this.openId = toolkit.GetQueryString("openId");
if(toolkit.GetQueryString("noticeSendId")) {
var noticeSendId = parseInt(toolkit.GetQueryString("noticeSendId"));
this.noticeClose = true;
this.getNoticeDetail(noticeSendId);
} else
this.getList();
},
mounted() {

window.addEventListener('scroll', () => {
var scrollTop = document.body.scrollTop;
if(scrollTop + window.innerHeight >= document.body.clientHeight) {

if(!this.scrollDisable && !this.snapDetailPage && !this.noMoreData) {
this.scrollDisable = true;
this.pageIndex += 1;
this.getList();
}

}
});
},
methods: {
getList() {
toolkit.showLoading(this.$vux);
var info = {
channel: this.channel,
openId: this.openId,
pageIndex: this.pageIndex,
pageCount: this.pageCount
}

toolkit.sendPost(config.serverBaseUrl + "/getNoticeList", info, (err, result) => {
this.scrollDisable = false;
if(err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var obj = JSON.parse(result);

if(obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {
if(obj.length > 0) {
this.lists.push(obj);
} else {
if(this.pageIndex > 1 && this.lists.length >= this.pageCount)
this.noMoreData = true;
}

toolkit.hideLoading(this.$vux);
}
}
});
},
getNoticeDetail(id) {

toolkit.showLoading(this.$vux);
var info = {
"noticeSendId": id, //Id
"openId": this.openId //openid
}

toolkit.sendPost(config.serverBaseUrl + "/readNotice", info, (err, result) => {
if(err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var obj = JSON.parse(result);

if(obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {
this.noticeDetail = obj;
this.detialView = true;
toolkit.hideLoading(this.$vux);
}
}
});
},
backList() {
this.detialView = false;
},
closeNotice() {
wx.closeWindow();
}
}
}



