
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

import moment from 'moment'
import toolkit from '../toolkit'
import config from '../config'

export default {
components: {
Group,
Cell,
CellFormPreview,
XButton,
Selector,
XTextarea,
Box,
Toast,
Flexbox,
FlexboxItem,
LoadMore,
Divider,
Timeline,
TimelineItem,
Flow,
FlowState, FlowLine
},
data() {
return {
channel: "",
openId: "",
lists: [],
leaseList: [],
leaseId: 0,
repairAdd: false,
repairContent: "",
repairView: false,
repairInfoView: {},
scrollDisable: false,
noMoreData: false,
pageIndex: 1,
pageCount: 20,
repairDetail: {}
}
},
created() {
this.openId = toolkit.GetQueryString("openId");
this.channel = toolkit.GetQueryString("channel");
this.getList();
this.getLeaseList();
},
mounted() {

window.addEventListener('scroll', () => {
var scrollTop = document.body.scrollTop;
if(scrollTop + window.innerHeight >= document.body.clientHeight) {

if(!this.scrollDisable && !this.snapDetailPage && !this.noMoreData) {
this.scrollDisable = true;
this.pageIndex += 1;
this.getList();
}
}
});
},
methods: {
getLeaseList() {
var info = {
channel: this.channel,
openId: this.openId
}

toolkit.showLoading(this.$vux);
toolkit.sendPost(config.serverBaseUrl + "/leaselist", info, (err, result) => {
if(err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var obj = JSON.parse(result);
if(obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {

if(obj.length > 0) {
for(var i = 0; i < obj.length; i++) {
this.leaseList.push({
key: obj[i].leaseId,
value: obj[i].houseName + "-" + obj[i].roomName
});
}
this.leaseId = obj[0].leaseId;
}
toolkit.hideLoading(this.$vux);
}
}
});
},
getList() {
toolkit.showLoading(this.$vux);
var info = {
channel: this.channel,
openId: this.openId,
pageIndex: this.pageIndex,
pageCount: this.pageCount
}
if(this.pageIndex == 1)
this.lists = [];

toolkit.sendPost(config.serverBaseUrl + "/getRepairList", info, (err, result) => {
this.scrollDisable = false;
if(err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var obj = JSON.parse(result);

if(obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {
if(obj.length > 0) {
this.lists.push(obj);
} else {
if(this.pageIndex > 1&&this.lists.length>this.pageCount)
this.noMoreData = true;
}
toolkit.hideLoading(this.$vux);
}
}
});
},
repairAdds() {
this.repairContent = "";
this.repairAdd = true;
},
repairSave() {
if(toolkit.trim(this.repairContent) == "") {
this.$vux.toast.show({
text: '',
type: 'warn'
})
return;
}

if(!this.leaseId){
if(this.leaseList.length == 0){
this.$vux.toast.show({
text: '',
type: 'warn'
})
}
else{
this.$vux.toast.show({
text: '',
type: 'warn'
})
}
return;
}

var info = {
channel: this.channel,
openId: this.openId,
content: this.repairContent,
leaseId: this.leaseId
}
toolkit.showLoading(this.$vux);
toolkit.sendPost(config.serverBaseUrl + "/addRepair", info, (err, result) => {
if(err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var obj = JSON.parse(result);
if(obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {
toolkit.hideLoading(this.$vux);
this.$vux.toast.show({
text: ''
})
this.pageIndex = 1;
this.getList();
this.repairAdd = false;
}
}
});
},
getRepairDetail(item) {
this.repairDetail = item;
if(item.repairStatus==2||item.repairStatus==3){
$(".vux-timeline-item-head").addClass("vux-timeline-item-head-first");
$(".vux-timeline-item-head .vux-timeline-item-checked").show();
}

this.repairView = true;
},
backList() {
this.repairAdd = false;
this.repairView = false;
}

}
}



