
import XButton from 'vux/src/components/x-button/index.vue'

import '@/assets/css/app.css'
import toolkit from "../toolkit";
import config from "../config";
export default {
components: {
XButton
},
data() {
return {
channel: "",
openId: "",
phone: "",
code: "",
bindCode: '',
};
},
created() {

this.openId = toolkit.GetQueryString("openId");
this.channel = toolkit.GetQueryString("channel");
this.phone = toolkit.GetQueryString("phone");
this.code = toolkit.GetQueryString("code");
},
methods: {

bindLease() {
if (!this.checkCode()) return
let info = {
channel: this.channel,
openId: this.openId,
phone: this.phone,
path: 1,
code: this.code,
bindPwd: this.bindCode
};
toolkit.showLoading(this.$vux);
toolkit.sendPost(
config.serverBaseUrl + "/verifyCode",
info,
(err, result) => {
if (err) {
this.$vux.alert.show({
title: "",
content: err
});
} else {
var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: "",
content: obj.errMsg
});
} else {
let bindObj = obj;
console.log(obj);

if (bindObj.newBindCount == 0 && bindObj.needPwdCount > 0) {
this.$vux.alert.show({
title: "",
content: ""
})
} else {
let url = window.location.origin + window.location.pathname + "?channel=" + this.channel + "&openId=" + this.openId +
"&pageCategory=0&bindRes=0&phone=" + this.phone + "&code=" + (bindObj.code ? bindObj.code : this.code) + "&needPwdCount=" + bindObj.needPwdCount;
window.location.replace(url)
}
}
}
toolkit.hideLoading(this.$vux);
}
);
},

checkCode() {
if (this.bindCode === "") {
this.$vux.toast.show({
text: "",
type: "warn",
width: "20em"
});
return false;
}
return true;
},
}
};



