
Number.prototype.toFixed = function (s) {
var that = this.toString(),
changenum, index;
if (s == 4) {
if (that.indexOf('.') >= 0) {
if (that.substring(that.indexOf('.') + 1).length < 2)
that = that + "0";
else if (that.substring(that.indexOf('.') + 1).length > 4) {
that = that.substring(0, that.indexOf('.') + 5);
}
} else {
that = that + ".00";
}
return that;
} else {

if (this < 0) {
that = -that;
}
changenum = (parseInt(that * Math.pow(10, s) + 0.5) / Math.pow(10, s)).toString();

index = changenum.indexOf(".");

if (index < 0 && s > 0) {
changenum = changenum + ".";
for (var i = 0; i < s; i++) {
changenum = changenum + "0";
}

} else {
index = changenum.length - index;
for (var i = 0; i < (s - index) + 1; i++) {
changenum = changenum + "0";
}
}

if (this < 0) {
return ("-" + changenum);
} else {
return changenum;
}
}
}

import FormPreview from 'vux/src/components/form-preview/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Box from 'vux/src/components/box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import moment from 'moment'
import toolkit from '../toolkit'
import config from '../config'
import '@/assets/css/app.css'
export default {
components: {
FormPreview,
Tab,
TabItem,
CellFormPreview,
Group,
Cell,
XButton,
Flexbox,
FlexboxItem,
Box,
XTextarea,
LoadMore,
Loading,
Divider,
Toast
},
data() {
return {
channel: "",
openId: "",
snapList: [],
snapDetailPage: false,
snapId: 0,
snapClose: false,
snapDetail: {},
lastBillDate: "",
isNoPay: false,
sTime: moment().format('YYYY-MM-DD HH:mm:ss'),
scrollDisable: false,
noMoreData: false,
needPayStatus: true,
needPayDesc: "",
billPayStatus: false,
billLastTimeStatus: false

}
},
created() {
this.openId = toolkit.GetQueryString("openId");
this.channel = toolkit.GetQueryString("channel");

if (toolkit.GetQueryString("snapId")) {
this.snapId = parseInt(toolkit.GetQueryString("snapId"));
this.snapClose = true;
this.getSnapDetail(this.snapId);
} else
this.getSnapList(true);
},
mounted() {
window.addEventListener('scroll', () => {
var scrollTop = document.body.scrollTop;
if (scrollTop + window.innerHeight >= document.body.clientHeight) {

if (!this.scrollDisable && !this.snapDetailPage && !this.noMoreData) {
this.scrollDisable = true;
this.getSnapList(this.isNoPay);
}
}
});
},
computed: {

},
methods: {
getNoPaySnapList() {
this.snapList = [];
this.sTime = moment().format('YYYY-MM-DD HH:mm:ss');
this.getSnapList(true);

},
getAllSnapList() {
this.snapList = [];
this.sTime = moment().format('YYYY-MM-DD HH:mm:ss');
this.getSnapList(false);
},
getSnapList(isNoPay) {
toolkit.showLoading(this.$vux);
var info = {
channel: this.channel,
openId: this.openId,
isNoPay: isNoPay,
sTime: this.sTime,
count: 10
}
this.isNoPay = isNoPay;
toolkit.sendPost(config.serverBaseUrl + "/snaplist", info, (err, result) => {
this.scrollDisable = false;
if (err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
toolkit.hideLoading(this.$vux);
} else {
if (obj.length > 0) {
this.setSnapList(obj);
} else {
if (this.sTime != moment(new Date()).format('YYYY-MM-DD HH:mm:ss'))
this.noMoreData = true;
toolkit.hideLoading(this.$vux);
}
}
}
});
},
setSnapList(obj) {
if (obj.length > 0) {
for (var i = 0; i < obj.length; i++) {
var snap = obj[i];
var snapJson = {
sId: snap.sId,
amount: snap.amount.toFixed(2),
list: [],
buttons: [{
style: 'default',
text: '',
onButtonClick: (name) => {
this.snapId = name;
this.getSnapDetail(this.snapId);
}
}]
};

switch (snap.category) {
case 0:
if (snap.billPeriods != -1) {
snapJson.limitDate = snap.billPeriods + "";
if (snap.type == 0) {
snapJson.limitDate += ""
} else if (snap.type == 1) {
snapJson.limitDate += ""
} else if (snap.type == 2) {
snapJson.limitDate += ""
}
snapJson.limitDate += " " + moment(snap.limitDate).format('YYYY-MM-DD');
} else
snapJson.limitDate = "";
break;
case 1:
snapJson.limitDate = "";
break;
case 2:
snapJson.limitDate = "";
break;
case 3:
snapJson.limitDate = "";
break;
case 4:
snapJson.limitDate = "";
break;
}
if (snap.state == 2) {
snapJson.desc = "";
} else if (snap.state == -4) {
snapJson.desc = "";
} else {
var limitDate = moment(snap.limitDate);
var nowDate = moment(new Date());
var a = moment([limitDate.year(), limitDate.month(), limitDate.date()]);
var b = moment([nowDate.year(), nowDate.month() + 1, nowDate.date()]);

if (moment(snap.limitDate).format('YYYY-MM-DD') < moment(new Date()).format('YYYY-MM-DD')) {
snapJson.desc = ":" + moment(moment(new Date()).format('YYYY-MM-DD')).diff(snap.limitDate, 'days') + "";
} else if (moment(snap.limitDate).format('YYYY-MM-DD') > moment(new Date()).format('YYYY-MM-DD')) {
snapJson.desc = ":" + -moment(moment(new Date()).format('YYYY-MM-DD')).diff(snap.limitDate, 'days') + "";
} else {
snapJson.desc = "";
}
}

snapJson.roomName = snap.houseName + "-" + snap.roomName;
this.snapList.push(snapJson);
this.sTime = moment(snap.sTime).format('YYYY-MM-DD HH:mm:ss');
}
}
toolkit.hideLoading(this.$vux);
},
getSnapDetail(snapId) {
toolkit.showLoading(this.$vux);
var info = {
channel: this.channel,
openId: this.openId,
sId: snapId
}
toolkit.sendPost(config.serverBaseUrl + "/fullsnapinfo", info, (err, result) => {
if (err) {
this.$vux.alert.show({
title: '',
content: err
})
toolkit.hideLoading(this.$vux);
} else {
var snapInfo = JSON.parse(result);
if (snapInfo.errCode) {
this.$vux.alert.show({
title: '',
content: snapInfo.errMsg
})
} else {
this.setSnapDetail(snapInfo);

}
toolkit.hideLoading(this.$vux);
}
});
},
startPay(sId) {
var info = {
sId: sId,
channel: this.channel,
openId: this.openId
}
var self = this;

toolkit.sendPost(config.serverBaseUrl + "/../pay/startpay", info, (err, result) => {
if (err) {
this.$vux.alert.show({
title: '',
content: err
})
} else {
var obj = JSON.parse(result);
if (obj.errCode) {
this.$vux.alert.show({
title: '',
content: obj.errMsg
})
} else {
WeixinJSBridge.invoke(
'getBrandWCPayRequest', JSON.parse(result),
function (res) {
if (res.err_msg == "get_brand_wcpay_request:ok") {


self.needPayStatus = false;
self.snapDetail.status = ""
self.billPayStatus = true;
self.snapDetail.billPayTime = moment().format('YYYY-MM-DD HH:mm:ss');
}
});
}
}
});
},
needPay() {

},

setSnapDetail(snapInfo) {
this.snapDetail.billSTime = moment(snapInfo.stime).format('YYYY-MM-DD HH:mm:ss');
this.needPayStatus = (snapInfo.state < 2 && snapInfo.state >= 0 && snapInfo.amount >= 0 && snapInfo.mchId && snapInfo.billIndex != 2);
this.billLastTimeStatus = false;
if (snapInfo.state == -4) {
this.needPayDesc = "";
this.billLastTimeStatus = true;
}






else if (snapInfo.state < 0)
this.needPayDesc = "";


switch (snapInfo.category) {
case 0:
if (snapInfo.billPeriods != -1) {
this.snapDetail.billCategoryTitle = snapInfo.billPeriods + "";
if (snapInfo.type == 0) {
this.snapDetail.billCategoryTitle += ""
} else if (snapInfo.type == 1) {
this.snapDetail.billCategoryTitle += ""
} else if (snapInfo.type == 2) {
this.snapDetail.billCategoryTitle += ""
}

} else
this.snapDetail.billCategoryTitle = "";
break;
case 1:
this.snapDetail.billCategoryTitle = "";
break;
case 2:
this.snapDetail.billCategoryTitle = "";
break;
case 3:
this.snapDetail.billCategoryTitle = "";
break;
case 4:
this.snapDetail.billCategoryTitle = "";
break;
}

this.snapDetail.category = snapInfo.category;
this.snapDetail.amount = snapInfo.amount.toFixed(2);
this.snapDetail.mchId = snapInfo.mchId;
this.snapDetail.list = [];
this.snapDetail.billDate = moment(snapInfo.startTime).format('YYYY-MM-DD') + "" + moment(snapInfo.endTime).format('YYYY-MM-DD');
this.snapDetail.state = snapInfo.state;

this.snapDetail.billLastTime = moment(snapInfo.lastTime).format('YYYY-MM-DD HH:mm:ss');
if (snapInfo.state == 0)
this.snapDetail.status = "";
if (snapInfo.state == 1)
this.snapDetail.status = "";
if (snapInfo.state == 2) {
this.snapDetail.status = "";
this.billPayStatus = true;
this.snapDetail.billPayTime = moment(snapInfo.payTime).format('YYYY-MM-DD HH:mm:ss');
}

if (snapInfo.state < 0)
this.snapDetail.status = "";
if (snapInfo.state == -4)
this.snapDetail.status = "";

this.snapDetail.roomName = snapInfo.houseName + "-" + snapInfo.roomName;
this.snapDetail.limitDate = moment(snapInfo.limitDate).format('YYYY-MM-DD');

if (snapInfo.rent > 0)
this.snapDetail.list.push({
label: "",
value: snapInfo.rent.toFixed(2)
});
if (snapInfo.billIndex == 2 || snapInfo.pledge != 0)

this.snapDetail.list.push({
label: "",
value: snapInfo.pledge.toFixed(2)
});
if (snapInfo.surplus > 0)
this.snapDetail.list.push({
label: "",
value: snapInfo.surplus.toFixed(2)
});
if (snapInfo.surplus < 0)
this.snapDetail.list.push({
label: "",
value: -snapInfo.surplus.toFixed(2)
});

if (snapInfo.category == 1) {
this.snapDetail.list.push({
label: snapInfo.billPeriods + "",
value: snapInfo.amount.toFixed(2)
});
}

if (snapInfo.details.length > 0) {
for (var i = 0; i < snapInfo.details.length; i++) {
var detail = snapInfo.details[i];
var feeName = detail.feeName;
if (detail.feeType == 1 && detail.readingDate != null) {
feeName += "    (:" + moment(detail.readingDate).format('YYYY-MM-DD') + ")";
}
var desc = "";
if (detail.feeType == 1) {
var feeUnitText = toolkit.GetFeeUnitText(detail.feeType, detail.feeUnit)
desc = " " + detail.price.toFixed(4) + "/" + feeUnitText + " ";
if (detail.noFloorAmount == 0) {
desc += ":" + detail.floorAmount + feeUnitText + " ";
}
desc += ":" + (detail.nowValue - detail.lastValue).toFixed(2) + feeUnitText + "(" + detail.lastValue + "~" + detail.nowValue + ")";
}
this.snapDetail.list.push({
label: feeName,
value: detail.totalMoney.toFixed(2),
desc: desc,
feeType: detail.feeType
});
}
}
this.snapDetail.remark = snapInfo.remark;
this.snapDetailPage = true;
this.snapId = snapInfo.sId;
toolkit.hideLoading(this.$vux);
},
backSnapList() {
this.snapDetailPage = false;
},
closeSnap() {
wx.closeWindow();
},
toAccount() {
this.startPay(this.snapId);
}
}
}



